import React, { ReactElement, useCallback, useState, useReducer, useEffect } from 'react';
import ChessCom, { loadChessComMatch } from '../chess/ChessCom';
import * as icons from '../assets/icons/';
import * as regexes from '../utils/regexes';
import { FaLink } from 'react-icons/fa';
import { PairingObject, Player, TournamentStatus } from '../interfaces';
import { GET } from '../utils/requests';

export interface PairingsProps {
	id: string
	status: TournamentStatus
	pairings: PairingObject[]
	playerDict: Map<string, Player>
}

export default function Pairings(props: PairingsProps): ReactElement {

    const [expanded, setExpanded] = useState('');

    const round = props.status.round || 0;

    const [timeDict, setTime] = useReducer((state: {[key: string]: (number | undefined)[]}, action: {
        id: string
        time: number
        players?: number
    }) => {
        let res = Object.assign({}, state);
        res[action.id] = [action.time, action.players];
        return res;
    }, {});

    const populateTimeDict = useCallback(async () => {
        for (let row of props.pairings) {
            let link = Object.values(row[2]).find(v => v);
            if (!link) continue;
            let matches = link.match(regexes.chessCom) as RegExpMatchArray;
            if (!matches) link = '';
            else link = 'https://www.chess.com/club/matches/live/' + matches[1];
            let id = Number(matches[1]);
            loadChessComMatch(id)
                .then((body) => setTime({id: matches[1], time: body.start_time * 1000}))
                .catch(async (e) => {
                    if (e.message.includes('not found')) {
                        let meta = await GET({
                            url: '/tournament/' + props.id + '/scrapeChessComTeamPage',
                            params: {
                                url: link
                            }
                        });
                        setTime({
                            id: matches[1],
                            time: new Date(meta['Start Time']).valueOf(),
                            players: meta['Players per Team']
                        });
                    }
                    else console.error(e);
                });
        }
	}, [props.pairings, setTime])
	
    useEffect(() => {
        populateTimeDict();
    }, [populateTimeDict]);

	return (
		<section>
							
			<p>All times in {new Date(Date.now()).toString().slice(25)}</p>
			<table className='download-table full-width'>
				<thead>
					<tr>
						<td style={{width: '27.5%'}}>White</td>
						<td style={{width: '12.5%'}}>Link</td>
						<td style={{width: '5%'}}></td>
						<td style={{width: '10%'}}>Start Time</td>
						<td style={{width: '5%'}}></td>
						<td style={{width: '12.5%'}}>Watch Live</td>
						<td style={{width: '27.5%'}}>Black</td>
					</tr>
				</thead>
				<tbody>                                
					{props.pairings.map((row, i) => {
						let id = [row[0].trim(), row[1].trim()].join('.')
						let w = props.playerDict.get(row[0].trim());
						let b = props.playerDict.get(row[1].trim());
						let link = Object.values(row[2]).find(v => v);
						let matches = link?.match(regexes.chessCom) as RegExpMatchArray;
						if (!matches) link = '';
						else link = 'https://www.chess.com/club/matches/live/' + matches[1];
						let time = matches && timeDict[matches[1]] ? timeDict[matches[1]][0] : 0;
						let enforceBoards: number | undefined = matches && timeDict[matches[1]] ? timeDict[matches[1]][1] : undefined;
						return (
							<>
								<tr key={['row', i].join('.')}>
									<td><div className='playerName'>{w?.firstName}</div></td>
									<td style={{padding: 0}}>
										{link ? <a className='button' href={link.trim()} target='_blank' rel='noopener noreferrer'>
											<FaLink />
										</a> : null}
									</td>                                                
									<td className={'bold'}>{w?.histories[round]?.game}</td>
									<td>{time ? new Date(time).toString().slice(0, 21) : '-'}</td>
									<td className={'bold'}>{b?.histories[round]?.game}</td>
									<td style={{padding: 0}}>
										{link ? <div className='button' onClick={() => setExpanded(expanded ? '' : id)}>
											<img className='icon' src={icons.watch} alt={'Watch games'}/>
										</div> : null}
									</td>
									<td><div className='playerName'>{b?.firstName}</div></td>
								</tr>
								{link && expanded === id? <tr>
									<td colSpan={100}>
										{row[2].chessComLink || row[2].chessComTeamLink ?
											row[2].chessComTeamLink ?
												<ChessCom matchURL={row[2].chessComTeamLink} enforceBoards={Number(enforceBoards)}/> :
												null :
											null
										}
									</td>
								</tr> : null}
							</>
						)
					})}
				</tbody>
			</table>
		</section>

	);

}