import React, { useReducer, useCallback, useEffect, ReactElement, useState } from 'react';
import FENBoard from 'fen-chess-board';
import ChessWebAPI, { BoardData, MatchData, Game, ChessPlayer } from 'chess-web-api';
import * as regexes from '../utils/regexes';
import styles from '../css/chesscom.module.css';
import { FaChessPawn, FaChessKing, FaChessKnight, FaChessBishop, FaChessRook, FaChessQueen, FaChessBoard } from 'react-icons/fa';
import { IoMdSquare, IoMdSquareOutline } from 'react-icons/io';
/*
FENBoard.constructor.prototype.flip = (): string[][] => {
    let fen = this.board;
    return this.board.map(row => row.reverse()).reverse();
}*/

const Chess = new ChessWebAPI({ queue: true });

export async function loadChessComGames(id: number, board: number): Promise<BoardData> {
    return new Promise((res, rej) => {
        Chess.dispatch(Chess.getTeamLiveMatchBoard, (data: { body: BoardData }, error) => {
            if (error) return rej(error);
            res(data.body);
        }, [id, board]);
    });
}

export async function loadChessComMatch(id: number): Promise<MatchData> {
    return new Promise((res, rej) => {
        Chess.dispatch(Chess.getTeamLiveMatch, (data: { body: MatchData }, error) => {
            if (error) return rej(error);
            res(data.body);
        }, [id]);
    });
}

export function colour(piece: string): 'W' | 'B' {
    if (piece.toLowerCase() === piece) return 'B';
    return 'W';
}

const Piece: Map<string, ReactElement> = new Map([
    ['p', <FaChessPawn />],
    ['n', <FaChessKnight />],
    ['b', <FaChessBishop />],
    ['r', <FaChessRook />],
    ['q', <FaChessQueen />],
    ['k', <FaChessKing />]
]);

interface ChessComProps {
    matchURL: string
    enforceBoards: number | undefined
}

export default function ChessCom(props: ChessComProps) {

    const [boards, setBoards] = useState(props.enforceBoards || 20);
    const [games, setGame] = useReducer((state: {game: Game, boardScores: {[key: string]: number}}[], action: {
        board: number,
        boardScores: {[key: string]: number}
        game: Game
    }) => {
        let arr = state.slice(0);
        arr[action.board] = {
            boardScores: action.boardScores,
            game: action.game
        };
        return arr;
    }, []);
    const [id, setID] = useState(0);
    const [teams, setTeam] = useReducer((state: [string, string], action: {
        name: string
        i: number
    }) => {
        state[action.i] = action.name;
        return state;
    }, ['', '']);
    const [teamDict, setTeamDict] = useReducer((state: {[key: string]: number}, action: {player: ChessPlayer, team: number}) => {
        let res = Object.assign({}, state);
        res[action.player.username.toLowerCase()] = action.team;
        return res;
    }, {});
    const [matchFinished, setMatchFinished] = useState(false);
    const [boardsFinished, setFinished] = useReducer((state: boolean[], action: {
       board: number,
       set: boolean
    }) => {
        let arr = state.slice(0);
        arr[action.board] = action.set;
        return arr;
    }, []);

    const fetchChessCom = useCallback(async () => {
        let matches = props.matchURL.match(regexes.chessCom);
        if (!matches) return console.error('Invalid URL: ', props.matchURL);
        let id = Number(matches[1]);
        try {
            let body = await loadChessComMatch(id);
            if (body.boards) setBoards(body.boards);
            if (body.status === 'finished' || body.status === 'scheduled') setMatchFinished(true);
            for (let t of Object.values(body.teams)) {
                let i = teams.findIndex(v => !v || v === t.name);
                setTeam({ name: t.name, i });
                for (let player of t.players) {
                    setTeamDict({
                        player,
                        team: i
                    });
                }
            }
        } catch (e) {
            console.error(e);
        }
        setID(id);
    }, [props.matchURL, teams]);

    useEffect(() => {
        fetchChessCom();
    }, [fetchChessCom]);

    const ping = useCallback(async () => {
        if (!id) return;
        if (matchFinished && games.length) return;
        for (let i = 1; i < boards + 1; i++) {
            if (boardsFinished[i]) continue;
            let x = loadChessComGames(id, i)
                .then((fetchedGames) => {
                    let game = fetchedGames.games[fetchedGames.games.length - 1];
                    setGame({
                        game: game,
                        boardScores: fetchedGames.board_scores,
                        board: i
                    });
                    if (Date.now() > game.end_time) setFinished({board: i, set: true});
                    return true;
                })
                .catch((e) => {
                    if (e.message.includes('doesn\'t have board')) {
                        setBoards(i - 1);
                        return false;
                    }
                    return true;
                });
            if (boards === (props.enforceBoards || 20)) {
                if (await x === false) break;
            }
        }

    }, [id, boards, matchFinished, boardsFinished, games.length])

    useEffect(() => {
        let x = setInterval(ping, 1000);
        return () => clearInterval(x);
    }, [ping]);

    if (!games.length && !boards) return <div>Match has not yet started!</div>

    return (
        <div className={styles.container}>
            {games.map((pairing, i) => {
                if (!pairing) return null;
                let scores = Object.entries(pairing.boardScores);
                let g = pairing.game;
                let flip = teamDict[g.black.username.toLowerCase()];    // team 1 is black
                let [w, b] = [
                    <>
                        {g.white.username} ({g.white.rating}){' '}
                        <IoMdSquareOutline />
                    </>,
                    <>
                        {g.black.username} ({g.black.rating}){' '}
                        <IoMdSquare />
                    </>
                ];
                if (flip) scores = scores.reverse();
                let fen = new FENBoard(g.fen);
                let board = fen.board;//flip ? fen.board : fen.flip;
                return (
                    <div className={styles.wrapper}>
                        <div className={styles.boardLabel}>
                            <span className='bold'>{scores[0][1]}</span>
                            <a href={'https://www.chess.com/live/game/' + g.url.split('/').pop()} target='_blank' rel='noopener noreferrer'>
                                <FaChessBoard />
                            </a>
                            <span className='bold'>{scores[1][1]}</span>
                        </div>
                        <div>{flip ? w : b}{' '}{teams[0]}</div>
                            <div className={styles.board}>
                                {board.map(line => {
                                    return (
                                        <div className={styles.row}>
                                            {line.map(p => {
                                                return (
                                                    <div className={[styles.square, styles[colour(p)]].join(' ')}>
                                                        {Piece.get(p.toLowerCase())}
                                                    </div>
                                                );
                                            })}
                                        </div>
                                    )
                                })}
                            </div>
                        <div>{flip ? b : w}{' '}{teams[1]}</div>
                    </div>
                );
            })}
        </div>
    );
}